* {
    margin: 0px;
    padding: 0px;
}

.hand {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow-x: scroll;
    display: flex;
    gap: 10px;
    background: black;
}

.hand > * {
    animation: slide-in 1s ease-out;
}

@keyframes slide-in {
    0% {
        transform: translateX(100vw);
    }
}